.rot-letter {
  float: left;
  text-align: center;
}

.rot-letter button {
  display: block;
  width: 30px;
  height: 30px;
}

.char-container {
  height: 30px;
  width: 30px;
  line-height: 30px;
  vertical-align: middle;
}

.rot-helper-controls {
  float: left;
  padding-right: 20px;
}

.rot-helper-letters {
  float: left;
}

.rot-helper-output {
  float: left;
  clear: left;
}


